@tailwind base;
@tailwind components;
@tailwind utilities;


html {
  scroll-behavior: smooth;
}
body{
  background-color: rgb(17 24 39);
}
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(56, 56, 56, 0.623);
  background-color: antiquewhite;
}

::-webkit-scrollbar-thumb {
  background: rgba(47, 0, 175, 0.836);
}

.img_ss{
  box-shadow: 0px 0px 20px 1px #dde00da2;

}

